import React, { useEffect, useRef, useState } from "react";
import Stack from '@mui/material/Stack';
import Slider from '@mui/material/Slider';
import VolumeDown from '@mui/icons-material/VolumeDown';
import VolumeUp from '@mui/icons-material/VolumeUp';
import WaveSurfer from "wavesurfer.js";
import PauseRounded from '@mui/icons-material/PauseRounded';
import PlayArrowRounded from '@mui/icons-material/PlayArrowRounded';
import IconButton from '@mui/material/IconButton';
import CircularProgress from '@mui/material/CircularProgress';
import Box from '@mui/material/Box';
import WaveFile from 'wavefile';

const formWaveSurferOptions = ref => ({
  container: ref,
  waveColor: '#9cceff',
  progressColor: '#1976d2',
  cursorColor: '#1976d2',
  barWidth: 2,
  barRadius: 3,
  barHeight:1,
  barGap:2,
  responsive: true,
});

export default function AudioWaveForm({ buffer }) {
  const waveformRef = useRef(null);
  const wavesurfer = useRef(null);
  const [playing, setPlay] = useState(false);
  const [volume, setVolume] = useState(0.5);
  const [loading,setLoading] = useState(true);

  useEffect(() => {
    setPlay(false);

    const options = formWaveSurferOptions(waveformRef.current);
    wavesurfer.current = WaveSurfer.create(options);

    let wav = new WaveFile(new Uint8Array(buffer));
    wav.fromIMAADPCM();
    let newBuf = wav.toBuffer();
    var blob = new Blob([newBuf], { 'type' : 'audio/wav' });
    wavesurfer.current.loadBlob(blob);

    wavesurfer.current.on("ready", function() {
      setLoading(false);
      if (wavesurfer.current) {
        wavesurfer.current.setVolume(0.5);
      }
    });
    return () => wavesurfer.current.destroy();
  }, [buffer]);

  const handlePlayPause = () => {
    setPlay(!playing);
    wavesurfer.current.playPause();
  };

  const onVolumeChange = e => {
    const { target } = e;
    const newVolume = +target.value;

    if (newVolume) {
      setVolume(newVolume);
      wavesurfer.current.setVolume(newVolume || 1);
    }
  };

  return (
    <>
    { loading && (
        <Box sx={{ display: 'flex',justifyContent:'center' }}>
          <CircularProgress />
        </Box>
    )}
      <div id="waveform" ref={waveformRef} />
      <Stack spacing={2} direction="column" alignItems="center" justifyContent="center">
      <IconButton
          aria-label={!playing ? 'play' : 'pause'}
          onClick={handlePlayPause}
          disabled={loading}
          >
            {!playing ? (
              <PlayArrowRounded
                sx={{ fontSize: '3rem' }}
                htmlColor="#000"
              />
            ) : (
              <PauseRounded sx={{ fontSize: '3rem' }} htmlColor="#000" />
            )}
        </IconButton>
        <Stack width={300} spacing={2} direction="row" sx={{ mb: 1 }} alignItems="center">
        <VolumeDown />
        <Slider 
        disabled={loading}
        id="volume"
        name="volume"
        aria-label="Volume" 
        value={volume} 
        onChange={onVolumeChange} 
        min={0.01}
        max={1}
        step={.025}/>
        <VolumeUp />
        </Stack>
      </Stack>
    </>
  );
}